import React from "react";

const InputRadio = ({defaultValue, labelTitle, updateType, updateFormValue, isChecked}: any) => {
    const handleChange = (val: any) => {
        updateFormValue({updateType, value: val})
    }
    return (
        <div>
            <input type="radio" checked={isChecked} name={updateType} value={defaultValue} onChange={(e) => handleChange(e.target.value)}/>
            <label htmlFor={updateType}> {labelTitle}</label>
        </div>
    )
}

export default InputRadio;
