import React, { useEffect, useState } from "react";
import { Box, Step, StepLabel, Stepper } from "@mui/material";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { Assignment, Face, Payment, People } from "@mui/icons-material";
import StepOne from "./component/StepOne";
import StepTwo from "./component/StepTwo";
import StepThree from "./component/StepThree";
import { StepFour } from "./component/StepFour";
import { useNavigate, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import completedIcon from '../../../assets/image/icon/completed.gif';
import opsError from '../../../assets/image/icon/ops-error.gif';
import {errorAlert, successAlert} from "../../../app/service/helper/handlingError";
import {fetchAddMember, fetchRegistrationMember} from "../../../app/service/action/memberAction";
import Loading from "../../../components/Loading/Loading";
import {RegistrationMemberDto} from "../../../app/service/dto/member/RegistrationMemberDto";
import {fetchParentAction} from "../../../app/service/action/parentAction";

const checkFormRequest = (activeStep: number, requestParam: any, setError: any) => {
    let status = true;
    if (activeStep === 1) {
        if (!requestParam.dataRider.fullname) {
            setError('Nama Lengkap wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.nickname) {
            setError('Nama Panggilan wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.photoRider) {
            setError('Photo Rider wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.gender) {
            setError('Jenis kelamin wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.noStart) {
            setError('Nomor Start/Jersey wajib diisi')
            status = false;
        }
        if (requestParam.dataRider.noStart.length > 3) {
            setError('Nomor Start/Jersey maksimal 3 digit')
            status = false;
        }

        if (!requestParam.dataRider.category) {
            setError('Category Rider wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.dob) {
            setError('Tanggal lahir wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.certificateBirtDay) {
            setError('Photo akte wajib diisi')
            status = false;
        }

        if (!requestParam.dataRider.sizeJersey) {
            setError('Size Jersey wajib diisi')
            status = false;
        }
    }
    if (activeStep === 2) {
        if (!requestParam.dataParent.email) {
            setError('E-mail wajib diisi')
            status = false;
        }

        if (!requestParam.dataParent.fatherName) {
            setError('Nama Ayah wajib diisi')
            status = false;
        }

        if (!requestParam.dataParent.fatherPhone) {
            setError('No. Telp/Hp Ayah wajib diisi')
            status = false;
        }

        if (!requestParam.dataParent.motherName) {
            setError('Nama Ibu wajib diisi')
            status = false;
        }

        if (!requestParam.dataParent.motherPhone) {
            setError('Nomor telp Ibu wajib diisi')
            status = false;
        }

        if (!requestParam.dataParent.address) {
            setError('Alamat wajib diisi')
            status = false;
        }
    }

    if (activeStep === 3) {
        if (!requestParam.payment) {
            setError('Bukti Pembayaran wajib diisi')
            status = false;
        }
    }
    return status;
};

export const RegistrationMember = () => {
    const navigate = useNavigate();
    const dispatch: any = useDispatch();
    const [requestParam, setRequestParams] = useState(RegistrationMemberDto)
    const [activeStep, setActiveStep] = useState(0);
    const [skipped, setSkipped] = useState(new Set());
    const [required, setRequired] = useState(false);
    const [loading, setLoading] = useState(false);
    const [finish, setFinish] = useState(false);
    const [error, setError] = useState('');
    const steps = [
        {
            icon: Assignment,
            label: 'Statement',
            component: <StepOne setRequired={setRequired} requestParam={requestParam}
                setRequestParams={setRequestParams} />
        },
        {
            icon: Face,
            label: 'Rider',
            component: <StepTwo requestParam={requestParam}
                setRequestParams={setRequestParams} />
        },
        {
            icon: People,
            label: 'Parent',
            component: <StepThree requestParam={requestParam}
                setRequestParams={setRequestParams} />
        },
        {
            icon: Payment,
            label: 'Payment',
            component: <StepFour requestParam={requestParam}
                setRequestParams={setRequestParams} />
        }
    ];

    const isStepOptional = (step: number) => {
        return step === 10;
    };

    const isStepSkipped = (step: number) => {
        return skipped.has(step);
    };

    const handleNext = () => {
        if (activeStep === steps.length - 1) {
            if (activeStep === 3) {
                if (requestParam.payment) {
                    handleSubmit()
                }
            }
        }

        let newSkipped = skipped;

        if (isStepSkipped(activeStep)) {
            newSkipped = new Set(newSkipped.values());
            newSkipped.delete(activeStep);
        }

        if (required) {
            const check = checkFormRequest(activeStep, requestParam, setError);
            if (check) {
                setActiveStep((prevActiveStep) => prevActiveStep + 1);
                setSkipped(newSkipped);
            } else {
                errorAlert(error)
            }
        } else {
            errorAlert("Mohon maaf, anda tidak memenuhi kriteria member kami. Silahkan melakukan komitmen terlebih dahulu");
        }
    };

    useEffect(() => {
        checkFormRequest(activeStep, requestParam, setError)
    }, [activeStep, requestParam]);

    const handleSubmit = () => {
        setLoading(true)
        if (userId) {
            // fetchAddMember
            // @ts-ignore
            dispatch(fetchAddMember(requestParam, userId))
                .then((response: any) => {
                    if (response.responseData.code === 200) {
                        setFinish(true)
                        successAlert("Registration success")
                        setLoading(false)
                    }
                    if (response.responseData.code === 400) {
                        errorAlert(response.responseData.message)
                        setActiveStep(activeStep - 1);
                    }

                })
                .catch((error: any) => {
                    errorAlert(error)
                    setLoading(false)
                    setFinish(false)
                })
        } else {
            // @ts-ignore
            dispatch(fetchRegistrationMember(requestParam))
                .then((response: any) => {
                    if (response.responseData.code === 200) {
                        setFinish(true)
                        successAlert("Registration success")
                        setLoading(false)
                    }
                    if (response.responseData.code === 400) {
                        errorAlert(response.responseData.message)
                        setActiveStep(activeStep - 1);
                    }

                })
                .catch((error: any) => {
                    errorAlert(error)
                    setLoading(false)
                    setFinish(false)
                })
        }
    }

    const handleBack = () => {
        setFinish(false)
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleSkip = () => {
        if (!isStepOptional(activeStep)) {
            // You probably want to guard against something like this,
            // it should never occur unless someone's actively trying to break something.
            throw new Error("You can't skip a step that isn't optional.");
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped((prevSkipped) => {
            const newSkipped = new Set(prevSkipped.values());
            newSkipped.add(activeStep);
            return newSkipped;
        });
    };

    const handleReset = () => {
        navigate('/', { replace: true })
        setActiveStep(0);
    };

    const responseData = useSelector(({ parentState }) => parentState.parent.data);
    const isLoadingParent = useSelector(({ parentState }) => parentState.parent.isLoading);

    const { userId } = useParams()
    useEffect(() => {
        if (userId) {
            dispatch(fetchParentAction())
        }
    }, [dispatch, userId]);

    useEffect(() => {
        if (userId && responseData.result) {
            setRequestParams((prevParams: any) => ({
                ...prevParams,
                dataParent: {
                    ...prevParams.dataParent,
                    fatherName: responseData.result.fatherName,
                    fatherPhone: responseData.result.fatherPhone,
                    motherName: responseData.result.motherName,
                    motherPhone: responseData.result.motherPhone,
                    address: responseData.result.address,
                    email: responseData.result.email
                }
            }));
        }
    }, [userId, responseData.result]);

    console.log(responseData)
    console.log(isLoadingParent)
    return (
        isLoadingParent && userId ? (<Loading />) : (
            <div className={'registration'}>
                <Stepper activeStep={activeStep} alternativeLabel>
                    {steps.map((label, index) => {
                        const stepProps: { completed?: boolean } = {};
                        const labelProps: {
                            optional?: React.ReactNode;
                        } = {};
                        if (isStepOptional(index)) {
                            labelProps.optional = (
                                <Typography variant="caption">Optional</Typography>
                            );
                        }
                        if (isStepSkipped(index)) {
                            stepProps.completed = false;
                        }
                        return (
                            <Step key={label.label} {...stepProps}>
                                <StepLabel {...labelProps}
                                // StepIconComponent={label.icon}
                                >
                                    {label.label}
                                </StepLabel>
                            </Step>
                        );
                    })}
                </Stepper>
                {activeStep === steps.length ? (
                    loading ? <Loading /> : (
                        finish ? (
                            <div>
                                <div className={'form-complete'}>
                                    <img src={completedIcon} alt="" />
                                    <p>
                                        All steps completed - you&apos;re finished.
                                    </p>
                                    <p>
                                        Please check you're E-Mail to activation account.
                                    </p>
                                </div>
                                <div className={'button-finish'}>
                                    <button className={'button-reset'} onClick={handleReset}>Home</button>
                                </div>
                            </div>
                        ) : (
                            <div>
                                <div className={'form-complete'}>
                                    <img src={opsError} alt="" />
                                    <p>
                                        All steps don't completed - you&apos;re not finish the registration.
                                    </p>
                                    <p>
                                        Please contact administration support.
                                    </p>
                                </div>
                                <div className={'button-finish'}>
                                    <button className={'button-reset'} onClick={handleReset}>Home</button>
                                </div>
                            </div>
                        )
                    )
                ) : (
                    <div>
                        <div>
                            {steps[activeStep].component}
                        </div>
                        <div className={'fixed-bottom'}>
                            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                                <button className={`button ${activeStep === 0 ? 'button-disabled' : ''}`}
                                    color="inherit"
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                >
                                    Back
                                </button>
                                <Box sx={{ flex: '1 1 auto' }} />
                                {isStepOptional(activeStep) && (
                                    <Button color="inherit" onClick={handleSkip} sx={{ mr: 1 }}>
                                        Skip
                                    </Button>
                                )}
                                <button onClick={handleNext} className={'button'}>
                                    {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                                </button>
                            </Box>
                        </div>
                    </div>
                )}
            </div>
        )
    )
}
