import React from "react";
import {InputFileImage} from "../../../../components/Input/InputFileImage";
import {checkImage} from "../../../../utils/Utils";
import formatRupiah from "../../../../utils/ConvertIDR";
import TitleComponent from "../../../../components/Title/TitleComponent";

export const StepFour = ({requestParam, setRequestParams}: any) => {

    const updateImageValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams({...requestParam, [updateType]: value});
    }
    return (
        <div className={'step-four'}>
            <div className="header">
                <TitleComponent title={'Pembayaran'}/>
            </div>
            <div className="content">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    <div className="info">
                        <div className={'text-center'}>
                            Silahkan melakukan transfer ke nomor rekening berikut: <br/>
                            <strong className={'text-center account-bank'}>
                                Bank BCA <br/>
                                5875454681 <br/>
                                a/n Silvia Noviarni
                            </strong>
                            <br/>
                            Jumlah yang di harus transfer <br/>
                            <strong
                                className={'payment'}> {formatRupiah(300000 + parseInt(requestParam.dataRider.noStart))}</strong>
                            <br/>
                            <br/>
                        </div>
                        <div>
                            <strong>
                                Contoh :<br/>
                                - Nomor punggung 59<br/>
                                - Total transfer Rp. 300.059
                            </strong>
                        </div>
                    </div>
                    <div className={'image-account'}>
                        <img src={checkImage(requestParam.payment)} alt=""/>
                    </div>
                    <InputFileImage
                        updateType={'payment'}
                        accept={'image/*'}
                        label={'Upload Payment Transfer'}
                        type={'file'}
                        updateFormValue={updateImageValue}/>
                </div>
            </div>
        </div>
    )
}
