import React, {useEffect, useState} from 'react';
import InputText from "../../../../components/Input/InputText";
import {InputFileImage} from "../../../../components/Input/InputFileImage";
import {checkImage} from "../../../../utils/Utils";
import {useDispatch, useSelector} from "react-redux";
import imageSize from '../../../../assets/image/size_jersey.png'
import {DATA_OPTION_GENDER, DATA_OPTION_JERSEY} from "../../../../utils/dummyData";
import {fetchAllMasterCategoryRaceAction} from "../../../../app/service/action/categoryRaceAction";
import SelectBox from "../../../../components/Input/SelectBox";
import TitleComponent from "../../../../components/Title/TitleComponent";

const StepTwo = ({requestParam, setRequestParams}: any) => {
    const dispatch: any = useDispatch();
    const [dataOptionCategory, setDataOptionCategory] = useState([]);
    const updateFormValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams((prevParams: any) => ({
            ...prevParams,
            dataRider: {
                ...prevParams.dataRider,
                [updateType]: value
            }
        }));
    }

    const updateImageValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams((prevParams: any) => ({
            ...prevParams,
            dataRider: {
                ...prevParams.dataRider,
                [updateType]: value
            }
        }));
    }

    const status = useSelector(({masterCategoryRaceState}: any) => {
        return masterCategoryRaceState.masterCategoryRace.status
    });

    const data = useSelector(({masterCategoryRaceState}: any) => {
        return masterCategoryRaceState.masterCategoryRace.data;
    })

    useEffect(() => {
        if (status === "ready" && data.result) {
            const update = data.result.map((item: any) => ({
                value: item.id,
                name: item.name
            }))
            setDataOptionCategory(update)
        }
    }, [data.result, status]);

    useEffect(() => {
        dispatch(fetchAllMasterCategoryRaceAction())
    }, [dispatch]);
    return (
        <div className="step-two">
            <div className="header">
                <TitleComponent title={'Data Rider'}/>
            </div>
            <div className="content">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    <div className={'image-account'}>
                        <img src={checkImage(requestParam.dataRider.photoRider)} alt=""/>
                    </div>
                    <InputFileImage
                        updateType={'photoRider'}
                        accept={'image/*'}
                        labelTitle={'Foto Rider'}
                        type={'file'}
                        updateFormValue={updateImageValue}/>
                    <InputText labelTitle={'Nama Lengkap'} updateType={'fullname'}
                               defaultValue={requestParam.dataRider.fullname}
                               updateFormValue={updateFormValue}/>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    <InputText labelTitle={'Nama Panggilan'} updateType={'nickname'}
                               defaultValue={requestParam.dataRider.nickname}
                               updateFormValue={updateFormValue}/>
                    <SelectBox labelTitle={'Jenis Kelamin'} defaultValue={requestParam.dataRider.gender}
                               updateFormValue={updateFormValue} updateType={'gender'}
                               options={DATA_OPTION_GENDER}
                               placeholder={"--Pilih Jenis Kelamin--"}/>
                    <InputText labelTitle={'No. Start/Jersey'} type={'number'} updateType={'noStart'}
                               defaultValue={requestParam.dataRider.noStart}
                               updateFormValue={updateFormValue}/>
                    <small className={'text-italic'}>Minimal 3 digit (Contoh: 009)</small>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    <InputText type={'date'} label={''}
                               updateType={'dob'}
                               labelTitle={'Tanggal Lahir'}
                               defaultValue={requestParam.dataRider.dob}
                               updateFormValue={updateFormValue}/>
                    <SelectBox labelTitle={'Kategori Rider'} defaultValue={requestParam.dataRider.category}
                               updateFormValue={updateFormValue} updateType={'category'}
                               placeholder={"--Pilih Kategori--"}
                               options={dataOptionCategory}/>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                    <div className={'image-account'}>
                        <img src={checkImage(requestParam.dataRider.certificateBirtDay)} alt=""/>
                    </div>
                    <InputFileImage
                        updateType={'certificateBirtDay'}
                        accept={'image/*'}
                        labelTitle={'Akte Kelahiran'}
                        type={'file'}
                        updateFormValue={updateImageValue}/>
                    <div className={'image-size'}>
                        <img src={imageSize} alt=""/>
                    </div>
                    <SelectBox labelTitle={'Size Jersey Rider'}
                               defaultValue={requestParam.dataRider.sizeJersey}
                               updateFormValue={updateFormValue} updateType={'sizeJersey'}
                               options={DATA_OPTION_JERSEY}
                               placeholder={"--Pilih Ukuran Jersey--"}/>
                </div>
            </div>
        </div>
    );
}

export default StepTwo;
