import React, {useEffect} from 'react';
import InputRadio from "../../../../components/Input/InputRadio";
import InputText from "../../../../components/Input/InputText";
import TitleComponent from "../../../../components/Title/TitleComponent";

const StepOne = ({setRequired, requestParam, setRequestParams}: any) => {

    const updateFormValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams((prevParams: any) => ({
            ...prevParams,
            statement: {
                ...prevParams.statement,
                [updateType]: value
            }
        }));
    }

    useEffect(() => {
        if (requestParam.statement.answerTwo === 'Ya' && requestParam.statement.answerThree === 'Ya') {
            setRequired(true)
        } else {
            setRequired(false)
        }
    }, [requestParam.statement.answerOne, requestParam.statement.answerThree, requestParam.statement.answerTwo, setRequired]);
    return (
        <div className="step-one">
            <div className="header">
                <TitleComponent title={'Statement'}/>
            </div>
            <div>
                <ol>
                    <li>
                        <p className={'text-bold-with-small'}>
                            {requestParam.statement.questionOne}
                        </p>
                        <InputRadio defaultValue="Ya" labelTitle="Ya" updateType={'answerOne'}
                                    updateFormValue={updateFormValue}
                                    isChecked={requestParam.statement.answerOne === 'Ya'}/>
                        {requestParam.statement.answerOne === 'Ya' && (
                            <div className="text-optional">
                                <p className={'mb-0'}>{requestParam.statement.optional}</p>
                                <InputRadio defaultValue="Ya" labelTitle="Ya" updateType={'answerOptional'}
                                            updateFormValue={updateFormValue}
                                            isChecked={requestParam.statement.answerOptional === 'Ya'}/>
                                <InputRadio defaultValue="Tidak" labelTitle="Tidak" updateType={'answerOptional'}
                                            updateFormValue={updateFormValue}
                                            isChecked={requestParam.statement.answerOptional === 'Tidak'}/>
                                {requestParam.statement.answerOptional === 'Tidak' ? (
                                    <div className="text-optional text-italic text-bold text-red">
                                        Hallo Riders silahkan untuk berkomitmen terlebih dahulu yaaah, untuk bisa
                                        bergabung jadi member kami 😎😎😎
                                    </div>
                                ) : (
                                    <InputText labelTitle={'*Nama komunitas sebelumnya jika ada'}
                                               updateType={'community'}
                                               updateFormValue={updateFormValue}
                                               defaultValue={requestParam.statement.community}/>
                                )}
                            </div>
                        )}
                        <InputRadio defaultValue="Tidak" labelTitle="Tidak" updateType={'answerOne'}
                                    updateFormValue={updateFormValue}
                                    isChecked={requestParam.statement.answerOne === 'Tidak'}/>
                    </li>
                    {requestParam.statement.answerOne === 'Tidak' || requestParam.statement.answerOptional === 'Ya' ? (
                        <li>
                            <p className={'text-bold-with-small'}>
                                {requestParam.statement.questionTwo}
                            </p>
                            <InputRadio defaultValue="Ya" labelTitle="Ya" updateType={'answerTwo'}
                                        updateFormValue={updateFormValue}
                                        isChecked={requestParam.statement.answerTwo === 'Ya'}/>
                            <InputRadio defaultValue="Tidak" labelTitle="Tidak" updateType={'answerTwo'}
                                        updateFormValue={updateFormValue}
                                        isChecked={requestParam.statement.answerTwo === 'Tidak'}/>
                        </li>
                    ) : ''}
                    {requestParam.statement.answerTwo === 'Ya' && (
                        <li>
                            <p className={'text-bold-with-small'}>
                                {requestParam.statement.questionThree}
                            </p>
                            <InputRadio defaultValue="Ya" labelTitle="Ya" updateType={'answerThree'}
                                        updateFormValue={updateFormValue}
                                        isChecked={requestParam.statement.answerThree === 'Ya'}/>
                            <InputRadio defaultValue="Tidak" labelTitle="Tidak" updateType={'answerThree'}
                                        updateFormValue={updateFormValue}
                                        isChecked={requestParam.statement.answerThree === 'Tidak'}/>
                        </li>
                    )}
                    {requestParam.statement.answerTwo === 'Ya' && requestParam.statement.answerThree === 'Ya' && (
                        <li>
                            <p className={'text-bold-with-small'}>
                                {requestParam.statement.questionFour}
                            </p>
                            <InputRadio defaultValue="Ya" labelTitle="Ya" updateType={'answerFour'}
                                        updateFormValue={updateFormValue}
                                        isChecked={requestParam.statement.answerFour === 'Ya'}/>
                            {requestParam.statement.answerFour === 'Ya' && (
                                <InputText label={requestParam.statement.optionalFour}
                                           updateType={'answerOptionalFour'}
                                           updateFormValue={updateFormValue}
                                           defaultValue={requestParam.statement.answerOptionalFour}/>

                            )}
                            <InputRadio defaultValue="Tidak" labelTitle="Tidak" updateType={'answerFour'}
                                        updateFormValue={updateFormValue}
                                        isChecked={requestParam.statement.answerFour === 'Tidak'}/>
                        </li>
                    )}
                </ol>
            </div>
        </div>
    );
}

export default StepOne;
