import React from 'react';
import InputText from "../../../../components/Input/InputText";
import TitleComponent from "../../../../components/Title/TitleComponent";

const StepThree = ({requestParam, setRequestParams}: any) => {

    const updateFormValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParams((prevParams: any) => ({
            ...prevParams,
            dataParent: {
                ...prevParams.dataParent,
                [updateType]: value
            }
        }));
    }
    return (
        <div className={'step-three'}>
            <div className="header">
                <TitleComponent title={'Data Orang Tua'}/>
            </div>
            <div className="content">
                <InputText labelTitle={'E-Mail'} updateType={'email'} type={'email'}
                           defaultValue={requestParam.dataParent.email}
                           updateFormValue={updateFormValue}/>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <InputText labelTitle={'Nama Ayah'} updateType={'fatherName'}
                               defaultValue={requestParam.dataParent.fatherName}
                               updateFormValue={updateFormValue}/>
                    <InputText labelTitle={'No. Hp/Whatsapp Ayah'} updateType={'fatherPhone'}
                               defaultValue={requestParam.dataParent.fatherPhone}
                               updateFormValue={updateFormValue}/>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <InputText labelTitle={'Nama Ibu'} updateType={'motherName'}
                               defaultValue={requestParam.dataParent.motherName}
                               updateFormValue={updateFormValue}/>
                    <InputText labelTitle={'No. Hp/Whatsapp Ibu'} updateType={'motherPhone'}
                               defaultValue={requestParam.dataParent.motherPhone}
                               updateFormValue={updateFormValue}/>
                </div>
                    <InputText labelTitle={'Alamat lengkap'} updateType={'address'}
                               defaultValue={requestParam.dataParent.address}
                               updateFormValue={updateFormValue}/>
                    <small className={'text-italic'}>*Digunakan untuk pengiriman jersey</small>
                </div>
            </div>
            );
            }
            export default StepThree;
